<script>
// import gSearch from '@/components/gSearch.vue'
import d3queSearch from '@/components/d3queSearch.vue'
import MenuCard from '@/components/MenuCard.vue'
import ChatMessage from '@/components/ChatMessage.vue'
import d3que from '@/components/d3que.vue'
import { getReply } from '@/api/api'
export default {
  components: {
    // gSearch,
    d3queSearch,
    MenuCard,
    d3que,
    ChatMessage
  },
  name: 'ZhiNengque',
  data() {
    return {
      message: '', // 用于存储用户输入的消息
      chatMessages: [
        { user: '机器人', message: '你好，请问您需要什么帮助？', timestamp: new Date().toLocaleString() }
      ],
      knowledgeCard: '医疗知识，为您解惑答疑，让健康更清晰！',
      showStaticImage: true,
      // d3jsonParser()处理 json 后返回的结果
      data: {
        nodes: [],
        links: []
      },
      names: ['疾病', '所属科室', '症状'],
      labels: ['Disease', 'Department', 'Symptom'],
      linkTypes: ['', 'department_is', 'has_symptom'],
      showGraph: false
    }
  },
  methods: {
    sendMessage() {
      const userMessage = { user: '用户', message: this.message, timestamp: new Date().toLocaleString() }
      this.chatMessages.push(userMessage)
      this.message = ''
      this.showGraph = true
      this.showStaticImage = false
      // this.sendToBackend(userMessage.message);
      // 建立与后端的连接，使用 EventSource
      const eventSource = new EventSource('http://10.174.99.181:8100/api/streamGeneration?question=' + encodeURIComponent(userMessage.message))


      let robotMessage = { user: '机器人', message: '', timestamp: '' };
      eventSource.onmessage = event => {
        // const partialReply = event.data
        // // 将部分回复添加到聊天框中
        // const robotMessage = { user: '机器人', message: partialReply, timestamp: new Date().toLocaleString() }
        // this.chatMessages.push(robotMessage)
        // 滚动到底部
        // this.scrollToBottom()

        const partialReply = event.data;
        if (!robotMessage.timestamp) {
          robotMessage.timestamp = userMessage.timestamp;
          this.chatMessages.push(robotMessage);
        }
        robotMessage.message += partialReply;
      }
      // 监听后端处理完整个问题后的回复
      eventSource.addEventListener('complete', event => {
        // const completeReply = event.data
        // 将完整的系统回复添加到聊天框中
        // const robotMessage = { user: '机器人', message: completeReply, timestamp: new Date().toLocaleString() }

        // this.chatMessages.push(robotMessage)
        const completeReply = event.data
        // 创建完整的机器人消息
        robotMessage = { user: '机器人', message: completeReply, timestamp: new Date().toLocaleString() }
        this.chatMessages.push(robotMessage)
        eventSource.close()
        // 滚动到底部
        this.scrollToBottom()
      })
      // 处理连接错误
      eventSource.onerror = () => {
        console.error('与后端建立连接时发生错误')
        // 关闭 EventSource 连接
        eventSource.close()
      }
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const chatBox = this.$refs.chatBox;
        if (chatBox) {
          chatBox.scrollTop = chatBox.scrollHeight;
        }
      });
    },
    // 最初问答逻辑
    sendToBackend(message) {
      // // 显示知识图谱
      this.showGraph = true

      // 后端接口调用返回问答信息
      getReply(message)
        .then(response => {
          // 后端返回了机器人的回复
          const robotReply = response.data;
          // 将机器人的回复添加到聊天记录中
          const robotMessage = { user: '机器人', message: robotReply, timestamp: new Date().toLocaleString() };
          this.chatMessages.push(robotMessage);
          this.displayMessage(robotMessage.message, 'robot');
          // 调用另一个后端接口来获取知识图谱数据
          // this.getKnowledgeGraphData(userMessage);
        })
        .catch(error => {
          // 处理发送消息失败的情况
          console.error('发送消息给后端失败:', error);
        })

    },
    // 视图更新
    update(json) {
      console.log('update')
      console.log(json)
      this.d3jsonParser(json)
    },
    /*eslint-disable*/
    // 解析json数据，主要负责数据的去重、标准化
    d3jsonParser(json) {
      const nodes = []
      const links = [] // 存放节点和关系
      const nodeSet = [] // 存放去重后nodes的id

      // // 使用vue直接通过require获取本地json，不再需要使用d3.json获取数据
      // d3.json('./../data/records.json', function (error, data) {
      //   if (error) throw error
      //   graph = data
      //   console.log(graph[0].p)
      // })

      for (let item of json) {
        for (let segment of item.p.segments) {
          // 重新更改data格式
          if (nodeSet.indexOf(segment.start.identity) == -1) {
            nodeSet.push(segment.start.identity)
            nodes.push({
              id: segment.start.identity,
              label: segment.start.labels[0],
              properties: segment.start.properties
            })
          }
          if (nodeSet.indexOf(segment.end.identity) == -1) {
            nodeSet.push(segment.end.identity)
            nodes.push({
              id: segment.end.identity,
              label: segment.end.labels[0],
              properties: segment.end.properties
            })
          }
          links.push({
            source: segment.relationship.start,
            target: segment.relationship.end,
            type: segment.relationship.type,
            properties: segment.relationship.properties
          })
        }
      }
      console.log(nodes)
      console.log(links)
      // this.links = links
      // this.nodes = nodes
      this.data = { nodes, links }
    },

    // 在接收到新消息时立即将其显示在聊天框中
    // displayMessage(message, sender, isLoading = false) {
    //   const chatBox = document.querySelector('.chat-messages');
    //   const messageDiv = document.createElement('div');
    //   messageDiv.classList.add('message', 'flex', 'items-center', 'space-x-2');

    //   if (isLoading) {
    //     // 显示加载中的效果
    //     messageDiv.innerHTML = `<div class="loader"></div>`;
    //   } else {
    //     // 使用 marked 库解析消息为 HTML 格式
    //     // const parsedHTML = marked(message, { sanitize: true });

    //     // if (sender === 'user') {
    //     //   messageDiv.classList.add('flex-row-reverse');
    //     //   messageDiv.innerHTML = `<img src="images/avatar.jpg" alt="用户头像" class="w-8 h-8 rounded-full"><div class="text-sm bg-blue-500 text-white rounded p-2"><p>${parsedHTML}</p></div>`;
    //     // } else {
    //     //   messageDiv.innerHTML = `<img src="images/人工智能.svg" alt="机器人头像" class="w-8 h-8 rounded-full"><div class="text-sm bg-gray-200 rounded p-2"><p>${parsedHTML}</p></div>`;
    //     // }
    //     if (sender === 'user') {
    //       messageDiv.classList.add('flex-row-reverse');
    //       messageDiv.innerHTML = `<img src="images/avatar.jpg" alt="用户头像" class="w-8 h-8 rounded-full"><div class="text-sm bg-blue-500 text-white rounded p-2"><p>${message}</p></div>`;
    //     } else {
    //       messageDiv.innerHTML = `<img src="images/人工智能.svg" alt="机器人头像" class="w-8 h-8 rounded-full"><div class="text-sm bg-gray-200 rounded p-2"><p>${message}</p></div>`;
    //     }
    //   }
    //   chatBox.appendChild(messageDiv);
    //   chatBox.scrollTop = chatBox.scrollHeight;

    //   // 如果是加载中，返回创建的 messageDiv，以便后续可以移除
    //   return messageDiv;

    // },


  }
}
